html, body {
  width: 250px;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.merged-popup {
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
}

h5 {
  text-align: center;
}

button {
  width: 100%;
  margin: top 5px;
  border: none;
  padding: 8px;
}

button:hover {
  background-color: #bcc6d1;
  box-shadow: 0 5px #0000004d;
}

input {
  border: none;
  width: 100%;
  margin-bottom: 10px;
  padding: 5px;
}

.output-box {
  overflow-wrap: break-word;
  width: 100%;
  min-height: 50px;
  margin-top: 10px;
  padding: 10px;
}

.merged-container {
  border: 1px solid #ccc;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.merged-popup {
  border: 1px solid #ccc;
  border-radius: 10px;
  overflow: hidden;
}
